import { mapGetters } from "vuex";
import PostList from "~/components/PostList.vue";
import ads from "~/components/ads.vue";
import ads2 from "~/components/ads2.vue";
import StarRating from "vue-star-rating";
const API_URL = process.env.API_URL;
const BASE_URL = process.env.BASE_URL;
export default {
  head() {
    let { title, tags, thumb } = this.$store.state.posts.detail;
    const keywords = [];
    for (let tag of tags) {
      keywords.push(tag.title);
    }
    const imgUrl = (thumb && thumb) || `${BASE_URL}/thumb.jpg`;
    const siteTitle = `${title}`;
    const description = `${keywords.join(", ")}, ${title}`;
    return {
      title: siteTitle,
      meta: [
        {
          hid: "og:site_name",
          name: "og:site_name",
          content: "travelcoffeehot.com",
        },
        {
          hid: "og:type",
          name: "og:type",
          content: "article",
        },
        {
          hid: "og:description",
          name: "og:description",
          content: description,
        },
        {
          hid: "og:title",
          name: "og:title",
          content: siteTitle,
        },
        {
          hid: "og:image",
          name: "og:image",
          content: imgUrl,
        },

        {
          hid: "twitter:card",
          name: "twitter:card",
          content: "article",
        },
        {
          hid: "twitter:description",
          name: "twitter:description",
          content: description,
        },
        {
          hid: "twitter:title",
          name: "twitter:title",
          content: siteTitle,
        },
        {
          hid: "twitter:image",
          name: "twitter:image",
          content: imgUrl,
        },

        {
          name: "title",
          content: siteTitle,
        },
        {
          name: "image",
          content: imgUrl,
        },
        {
          name: "keywords",
          content: `${description}, ${title}`,
        },
        {
          name: "description",
          content: description,
        },
      ],
    };
  },
  watchQuery: ["page", "s"],
  async asyncData({ store, params }) {
    await store.dispatch("posts/getOne", {
      slug: params.slug,
    });
  },
  data: function () {
    const params = this.$route.query;
    const media = [];
    let { title, tags, rate, totalRate } = this.$store.state.posts.detail;
    return {
      postImages: [],
      params: params,
      media,
      index: null,
      rate: {
        "@context": "https://schema.org/",
        "@type": "CreativeWorkSeries",
        name: title,
        aggregateRating: {
          "@type": "AggregateRating",
          bestRating: 5,
          ratingValue: rate,
          ratingCount: totalRate,
          worstRating: 0,
        },
      },
    };
  },
  components: {
    PostList,
    StarRating,
    ads,
    ads2,
  },
  methods: {},
  computed: {
    ...mapGetters({
      post: "posts/getOne",
    }),
  },
  mounted() {
    if (this.$store.state.posts.detail) {
      fetch(`${API_URL}/posts/images`, {
        method: "POST",
        body: new URLSearchParams({
          postId: this.$store.state.posts.detail._id,
        }),
      }).then((res) =>
        res.json().then((res) => {
          if (res) {
            for (let img of res) {
              if (img.link) {
                this.media.push({
                  url: img.link,
                  title: "",
                });
              }
            }
          }
          this.postImages = res;
        })
      );
    }
  },
};
